var _self;
export default{
	data(){
		return {
			openSettingBtnHidden: true,//是否授权
			storeId:0,
			shopInfo:{
				qrcode:"",
				storeinfo:{
					backgroundimg:"",
					storename:"",
					id:""
				}
			}
		}
	},
	components:{
	},
	onLoad(opt) {
		_self = this;
		if(opt.id != undefined){
			_self.storeId = opt.id;
			_self.getEwm();
		}
	},
	methods:{
		//微信小程序保存到相册
		handleSetting(e){
			if (!e.detail.authSetting['scope.writePhotosAlbum']) {
			      _self.openSettingBtnHidden = false;
			} else {
				 _self.openSettingBtnHidden = true;
			}
		},
		//获取推荐码
		getEwm(){
			uni.request({
			    url: _self.$httpUrl+'index.php/api/Store/getStoreqrcode', 
				method:"POST",
				data:{
					storeid:_self.storeId
				},
			    success: (res) => {
			     
			      console.log(res);
				  
				  if(res.data.status == 1){
					_self.shopInfo = res.data.result;
				  }else{
					  uni.showToast({
					  	icon:"none",
						title:res.data.msg
					  })
				  }
				  
			    },fail: (e) => {
					
			    },complete: (e) => {
					
			    }
			});
		},
		saveEwm:function(e){
			//获取相册授权
			   uni.getSetting({
			     success(res) {
			       if (!res.authSetting['scope.writePhotosAlbum']) {
			         uni.authorize({
			           scope: 'scope.writePhotosAlbum',
			           success() {
			             //这里是用户同意授权后的回调
			             _self.saveImgToLocal();
			           },
			           fail() {//这里是用户拒绝授权后的回调
			               _self.openSettingBtnHidden=false
			           }
			         })
			       } else {//用户已经授权过了
			         _self.saveImgToLocal();
			       }
			     }
			   })
		},
		saveImgToLocal:function(e){
			
			uni.showModal({
			    title: '提示',
			    content: '确定保存到相册吗',
			    success: function (res) {
			        if (res.confirm) {
						
						uni.downloadFile({
								url: _self.shopInfo.qrcode,//图片地址
								success: (res) =>{
									if (res.statusCode === 200){
										uni.saveImageToPhotosAlbum({
											filePath: res.tempFilePath,
											success: function() {
												uni.showToast({
													title: "保存成功",
													icon: "none"
												});
											},
											fail: function() {
												uni.showToast({
													title: "保存失败",
													icon: "none"
												});
											}
										});
									} 
								}
							})
						
						
			        } else if (res.cancel) {
						
			        }
			    }
			});
			
		}
	}
}

